
'use client';

import React from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import type { Repair, RepairStatus } from '@/lib/types';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';


interface ArchivedRepairsListProps {
  repairs: Repair[];
}

export function ArchivedRepairsList({ repairs }: ArchivedRepairsListProps) {

  const getStatusClass = (status: RepairStatus) => {
    switch (status) {
      case 'Archived':
        return 'bg-gray-100 text-gray-800 border-gray-200 hover:bg-gray-200 dark:bg-gray-800/50 dark:text-gray-300 dark:border-gray-700/60';
      default:
        return '';
    }
  };

  const archivedRepairs = repairs.filter(r => r.status === 'Archived');

  return (
    <Card>
      <CardHeader>
        <CardTitle>Archived Repairs</CardTitle>
        <CardDescription>
          A list of completed and archived repair jobs.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Customer</TableHead>
              <TableHead className="hidden sm:table-cell">Device</TableHead>
              <TableHead className="hidden lg:table-cell">Serviced By</TableHead>
              <TableHead className="hidden md:table-cell">Issue</TableHead>
              <TableHead className="hidden sm:table-cell">Created</TableHead>
              <TableHead>Status</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {archivedRepairs.length > 0 ? archivedRepairs.map((repair) => (
              <TableRow key={repair.id}>
                <TableCell>
                    <div className="font-medium">{repair.customerName}</div>
                    <div className="text-sm text-muted-foreground md:hidden">{repair.brand} {repair.model}</div>
                </TableCell>
                <TableCell className="hidden sm:table-cell">{repair.brand} {repair.model}</TableCell>
                <TableCell className="hidden lg:table-cell">{repair.assignedToName}</TableCell>
                <TableCell className="hidden md:table-cell max-w-xs truncate">{repair.problemNotes}</TableCell>
                <TableCell className="hidden sm:table-cell">{format(repair.createdAt, 'PP')}</TableCell>
                <TableCell>
                  <Badge variant="outline" className={cn("border", getStatusClass(repair.status))}>{repair.status}</Badge>
                </TableCell>
              </TableRow>
            )) : (
              <TableRow>
                <TableCell colSpan={6} className="text-center h-24">
                  No archived repairs.
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
}
