'use client';

import React, { useState } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { Search } from 'lucide-react';
import type { Customer } from '@/lib/types';
import { placeholderCustomers } from '@/lib/placeholder-data';
import { useToast } from '@/hooks/use-toast';
import { ScrollArea } from '../ui/scroll-area';

interface CustomerCardProps {
  onCustomerSelect: (customer: Customer) => void;
}

export function CustomerCard({ onCustomerSelect }: CustomerCardProps) {
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState<Customer[]>([]);
  const [customers, setCustomers] = useState<Customer[]>(placeholderCustomers);
  
  const [custName, setCustName] = useState('');
  const [custPhone, setCustPhone] = useState('');
  const [custEmail, setCustEmail] = useState('');

  const { toast } = useToast();

  const handleSearch = () => {
    if (!searchTerm.trim()) {
      setSearchResults([]);
      return;
    }
    const lowercasedTerm = searchTerm.toLowerCase();
    const results = customers.filter(
      (c) =>
        c.fullName.toLowerCase().includes(lowercasedTerm) ||
        c.email.toLowerCase().includes(lowercasedTerm) ||
        c.phone.includes(lowercasedTerm)
    );
    setSearchResults(results);
  };

  const handleAddCustomer = () => {
    if(!custName || (!custEmail && !custPhone)) {
        toast({
            title: 'Missing Information',
            description: 'Please provide a name and at least an email or phone number.',
            variant: 'destructive',
        });
        return;
    }

    const newCustomer: Customer = {
        id: `cust-${Date.now()}`,
        fullName: custName,
        email: custEmail,
        phone: custPhone,
        createdAt: new Date(),
    };

    setCustomers(prev => [newCustomer, ...prev]);
    onCustomerSelect(newCustomer);

    toast({
        title: 'Customer Added',
        description: `${custName} has been added and selected.`,
    });

    setCustName('');
    setCustPhone('');
    setCustEmail('');
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Customer</CardTitle>
        <CardDescription>
          Search for an existing customer or add a new one.
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="space-y-2">
          <Label htmlFor="customerSearch">Search by Name/Email/Phone</Label>
          <div className="flex gap-2">
            <Input
              id="customerSearch"
              placeholder="Type to search…"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
            />
            <Button onClick={handleSearch} aria-label="Search Customers">
              <Search className="h-4 w-4" />
            </Button>
          </div>
        </div>

        <ScrollArea className="h-48 rounded-md border">
          <div className="p-4">
            {searchResults.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-4">No results. Try searching.</p>
            ) : (
              <div className="space-y-2">
                {searchResults.map((customer) => (
                  <div
                    key={customer.id}
                    className="flex items-center justify-between rounded-md border p-3"
                  >
                    <div>
                      <p className="font-medium">{customer.fullName}</p>
                      <p className="text-sm text-muted-foreground">
                        {customer.email}
                      </p>
                    </div>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => onCustomerSelect(customer)}
                    >
                      Select
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </div>
        </ScrollArea>

        <div className="space-y-4 pt-4 border-t">
          <h3 className="font-medium text-lg">Add New Customer</h3>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <div className="space-y-2">
              <Label htmlFor="custName">Full Name</Label>
              <Input id="custName" value={custName} onChange={(e) => setCustName(e.target.value)} />
            </div>
            <div className="space-y-2">
              <Label htmlFor="custPhone">Phone</Label>
              <Input id="custPhone" type="tel" value={custPhone} onChange={(e) => setCustPhone(e.target.value)} />
            </div>
          </div>
          <div className="space-y-2">
            <Label htmlFor="custEmail">Email</Label>
            <Input id="custEmail" type="email" value={custEmail} onChange={(e) => setCustEmail(e.target.value)} />
          </div>
          <Button onClick={handleAddCustomer} className="w-full">
            Add Customer
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}
