'use client';

import React, { useState, useRef, useEffect } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
  CardFooter,
} from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import SignaturePad, { SignaturePadRef } from '@/components/signature-pad';
import type { Customer, Repair, Technician } from '@/lib/types';
import { useToast } from '@/hooks/use-toast';
import {
  Computer,
  Smartphone,
  Tablet,
  Gamepad,
  AlertCircle,
  User,
} from 'lucide-react';
import { useUser } from '@/context/UserContext';

interface RepairCardProps {
  selectedCustomer: Customer | null;
  onCreateRepair: (repair: Omit<Repair, 'id' | 'createdAt'>) => void;
  technicians: Technician[];
}

const deviceTypes = [
  { value: 'Computer', icon: Computer },
  { value: 'Phone', icon: Smartphone },
  { value: 'Tablet', icon: Tablet },
  { value: 'Console', icon: Gamepad },
  { value: 'Other', icon: AlertCircle },
];

const brandOptions = [
    'Apple',
    'Samsung',
    'Google',
    'Dell',
    'HP',
    'Lenovo',
    'Sony',
    'LG',
    'Microsoft',
    'Nintendo',
    'Other'
];

export function RepairCard({ selectedCustomer, onCreateRepair, technicians }: RepairCardProps) {
  const { user } = useUser();
  const [deviceType, setDeviceType] = useState('');
  const [brand, setBrand] = useState('');
  const [model, setModel] = useState('');
  const [accessories, setAccessories] = useState('');
  const [problemNotes, setProblemNotes] = useState('');
  const [assignedToName, setAssignedToName] = useState('');
  const signatureRef = useRef<SignaturePadRef>(null);

  const { toast } = useToast();

  const resetForm = () => {
    setDeviceType('');
    setBrand('');
    setModel('');
    setAccessories('');
    setProblemNotes('');
    if (user?.role === 'Admin' && technicians.length > 0) {
      setAssignedToName(technicians[0].email);
    } else if (user?.role === 'Student') {
      setAssignedToName(user.email);
    } else {
      setAssignedToName('');
    }
    signatureRef.current?.clear();
  }

  useEffect(() => {
    resetForm();
  }, [selectedCustomer]);
  
  useEffect(() => {
    if (user?.role === 'Admin') {
      if (technicians.length > 0 && !technicians.find(t => t.email === assignedToName)) {
        setAssignedToName(technicians[0].email);
      } else if (technicians.length === 0) {
        setAssignedToName('');
      }
    } else if (user?.role === 'Student') {
      setAssignedToName(user.email);
    }
  }, [technicians, user, assignedToName]);


  const handleCreateRepair = () => {
    if (!selectedCustomer) {
      toast({
        title: 'No Customer Selected',
        description: 'Please search for and select a customer first.',
        variant: 'destructive',
      });
      return;
    }

    if (!deviceType || !brand || !model || !problemNotes) {
        toast({
            title: 'Missing Information',
            description: 'Please fill out all device and problem details.',
            variant: 'destructive',
        });
        return;
    }
    
    if(!signatureRef.current?.getSignature()) {
        toast({
            title: 'Signature Required',
            description: 'Please have the customer sign for liability.',
            variant: 'destructive',
        });
        return;
    }

    if (!assignedToName) {
      toast({
        title: 'No Technician Assigned',
        description: 'Please assign a technician to this repair.',
        variant: 'destructive',
      });
      return;
    }

    const newRepair = {
        customerId: selectedCustomer.id,
        customerName: selectedCustomer.fullName,
        deviceType,
        brand,
        model,
        accessories: accessories.split(',').map(s => s.trim()).filter(Boolean),
        problemNotes,
        status: 'Pending' as const,
        signatureURL: signatureRef.current.getSignature(),
        assignedToName,
    };
    
    onCreateRepair(newRepair);
    
    toast({
        title: 'Repair Ticket Created',
        description: `A new repair for ${selectedCustomer.fullName} has been created.`,
    });

    resetForm();
  };

  const isAdmin = user?.role === 'Admin';
  const loggedInTechnician = technicians.find(t => t.email === user?.email);


  return (
    <Card>
      <CardHeader>
        <CardTitle>New Repair Intake</CardTitle>
        <CardDescription>
          Fill in the details for the new repair job.
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="selectedCustomer">Selected Customer</Label>
          <Input
            id="selectedCustomer"
            disabled
            value={
              selectedCustomer
                ? `${selectedCustomer.fullName} - ${selectedCustomer.email}`
                : 'Choose from search results…'
            }
          />
        </div>

        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
          <div className="space-y-2">
            <Label htmlFor="deviceType">Device Type</Label>
            <Select value={deviceType} onValueChange={setDeviceType}>
              <SelectTrigger id="deviceType">
                <SelectValue placeholder="Select device type" />
              </SelectTrigger>
              <SelectContent>
                {deviceTypes.map(d => (
                   <SelectItem key={d.value} value={d.value}>
                     <div className="flex items-center gap-2">
                       <d.icon className="h-4 w-4 text-muted-foreground" />
                       <span>{d.value}</span>
                     </div>
                   </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="space-y-2">
            <Label htmlFor="brand">Brand</Label>
            <Select value={brand} onValueChange={setBrand}>
                <SelectTrigger id="brand">
                    <SelectValue placeholder="Select brand" />
                </SelectTrigger>
                <SelectContent>
                    {brandOptions.map(b => (
                        <SelectItem key={b} value={b}>{b}</SelectItem>
                    ))}
                </SelectContent>
            </Select>
          </div>
        </div>

        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
          <div className="space-y-2">
            <Label htmlFor="model">Model</Label>
            <Input id="model" placeholder="e.g., iPhone 14, XPS 15" value={model} onChange={e => setModel(e.target.value)} />
          </div>
          <div className="space-y-2">
            <Label htmlFor="accessories">Accessories</Label>
            <Input id="accessories" placeholder="e.g., charger, case" value={accessories} onChange={e => setAccessories(e.target.value)} />
          </div>
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="assignedTo">Serviced By</Label>
          <Select value={assignedToName} onValueChange={setAssignedToName} disabled={!isAdmin}>
            <SelectTrigger id="assignedTo">
                <SelectValue placeholder="Select technician" />
            </SelectTrigger>
            <SelectContent>
                {isAdmin ? (
                    technicians.map(t => (
                        <SelectItem key={t.email} value={t.email}>
                            <div className="flex items-center gap-2">
                                <User className="h-4 w-4 text-muted-foreground" />
                                <span>{t.name}</span>
                            </div>
                        </SelectItem>
                    ))
                ) : (
                    loggedInTechnician && (
                        <SelectItem key={loggedInTechnician.email} value={loggedInTechnician.email}>
                            <div className="flex items-center gap-2">
                                <User className="h-4 w-4 text-muted-foreground" />
                                <span>{loggedInTechnician.name}</span>
                            </div>
                        </SelectItem>
                    )
                )}
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label htmlFor="problemNotes">Problem Notes</Label>
          <Textarea
            id="problemNotes"
            placeholder="Describe the issue in detail…"
            value={problemNotes}
            onChange={e => setProblemNotes(e.target.value)}
          />
        </div>

        <div className="space-y-2">
          <Label>Customer Liability Signature</Label>
          <SignaturePad ref={signatureRef} width={500} height={200} />
        </div>
      </CardContent>
      <CardFooter className="flex justify-between gap-2">
        <Button variant="outline" onClick={() => signatureRef.current?.clear()}>
          Clear Signature
        </Button>
        <Button onClick={handleCreateRepair}>Create Repair Ticket</Button>
      </CardFooter>
    </Card>
  );
}
